.btnA {

    position: absolute;
    top: 665px;
    left: 49%;

    transform: translate(-50%, -50%);
    padding: 28px 30px;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-size: 20px;
    height: 80px;
}

.btnA:hover {
    color: #0ab80a;
    text-decoration: none;
}

.btnA:before {

    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background: white;
    z-index: -1;
    transition: transform 0.5s;
    transform-origin: bottom right;
    transform: scale(0);

}

.btnA:hover:before {
    transition: transform 0.5s;
    transform-origin: top left;
    transform: scale(1);
    border-radius: 8px;
}

.btnA:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    background: transparent;
    border: 3px solid #fff;
    box-sizing: border-box;
    z-index: -1;
    transition: transform 0.5s;
    transform-origin: top left;
    transform: scale(1);
    border-radius: 8px;
}

.btnA:hover:after {

    transition: transform 0.5s;
    transform-origin: bottom right;
    transform: scale(0);
}

